/* @import url(''); */
@font-face{
	font-family : "Futura Medium";
	src:url("../../fonts/futura/futura medium bt.ttf") format('truetype');
}
.landing {
	display: inline-block;
	margin:auto;
	word-spacing: 30px;
	z-index: 10;
	width: 100%;
	position: relative;
}
.container{
	max-width: 1280px;
	width: 100%;
	display: flex;
	flex-direction: column;
	position: relative;
}

.fitContent{
	display:flex;
	flex-direction:column;
	position: relative;
	overflow: hidden;
	margin:auto;
}
.blur{
	filter: blur(2px);
	-webkit-filter: blur(2px);
	position: absolute;
	width:100%;
	z-index: 0;
	max-width: 100%;
	top:-15%;
}
.overlay {
	position: absolute;
	top: 0;
	left: 0;
width:100%;
	z-index: 11;
	;
	height: 100%;
	background: linear-gradient(to right, rgba(192,52,247,0.78) 0%, rgba(192,52,247,1) 0%, rgba(159,50,234,0) 28%, rgba(109,46,213,0) 71%, rgba(75,44,199,1) 100%);

}


.exploreMoreButton{
	background-color: transparent;
	position: absolute;
	font-weight: bold;
	bottom: 20px;
	right: 20px;
	border: solid white 1px;
	z-index: 100;
	transition: ease 0.5s;
	border-radius:5px;
}
.exploreMoreButton:hover{

	background-color: white;
	color: #d52349 !important;
}

.leftBottomSide{
	position: absolute;
	bottom:30px;
	left:50px;
	font-weight: black;
	font-size: 50px !important;
	z-index: 11;
	font-weight: small;
	color:white !important;
	text-align: right;
	font-family: "Futura Medium"
}